@import url('https://fonts.googleapis.com/css2?family=Gowun+Dodum&display=swap');

:root {
  --font: 'Gowun Dodum', sans-serif;
}

body,
html {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  overflow-x: hidden;
}

section {
  padding: 1rem;
}

nav {
  position: fixed;
  top: 0;
  right: 5rem;
  width: 30%;
  height: 4rem;
  z-index: 3333;
}

nav ul {
  display: flex;
  justify-content: space-around;
}

.navbar li {
  display: flex;
  align-items: center;
  cursor: pointer;
  border-radius: 1rem;
  font-weight: bold;
}

.navbar li:hover,
.navbar li:hover .nav-text,
.navbar li.active .nav-text,
.icon.active {
  color: orange;
  opacity: 1;
}

.icon {
  font-size: 20px;
  margin-right: 8px;
  transition: color 0.3s;
  color: lemonchiffon;
}

.nav-text {
  opacity: 0;
  transition: opacity 0.3s;
}

.active {
  color: orange;
}

/* main------------------------------------------------ */
.main-bg {
  display: flex;
  height: 100vh;
  background-size: cover;
  background-color: #fff;
}

.main-left {
  width: 35rem;
  height: 40rem;
  margin: 2rem
}

.main-left img {
  width: 100%;
  height: 100%;
  border-radius: 1rem;
  margin-left: 6rem;
  box-shadow: 10px 10px 5px 0px rgba(0, 0, 0, 0.69);
}

.main-right {
  width: 100%;
  color: black;
  text-align: center;
  font-family: var(--font);
  margin-top: 4rem;
}

.main-text {
  margin-top: 5rem;
  font-size: 1.5rem;
}

.main-btn {
  border: none;
  display: block;
  text-align: center;
  cursor: pointer;
  overflow: hidden;
  position: relative;
  color: #fff;
  font-weight: 700;
  font-size: 15px;
  background-color: #222;
  padding: 17px 60px;
  margin: 5rem auto;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.20);
  border-radius: 20px;
}

.main-btn span {
  position: relative;
  z-index: 1;
}

.main-btn::after {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 200%;
  height: 500%;
  background: #78c7d2;
  transition: all .4s ease-in-out;
  transform: translateX(-98%) translateY(-25%) rotate(45deg);
}

.main-btn:hover::after {
  transform: translateX(-9%) translateY(-25%) rotate(45deg);
  transform: translateX(-9%) translateY(-25%) rotate(45deg);
}

/* skills------------------------------------------ */
.about {
  background-color: rgb(34, 40, 49);
  color: #fff;
}

.about-header {
  text-align: center;
  margin-top: 4rem;
  font-size: 3.2rem;
  height: 1rem;
}

.about-header span {
  color: #78c7d2;
}

.skills-header {
  display: flex;
  width: 80%;
  margin: 8rem auto 3rem;
  justify-content: space-evenly;
}

.skills-list {
  border: 0.2rem solid white;
  font-weight: 500;
  padding: 0.1rem 0.3rem;
  border-radius: 1rem;
  color: white;
}

.skills-container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  height: 100%;
}

.skills-box {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 2rem auto 2rem;
  border: 0.2rem solid rgb(0, 173, 181);
  border-radius: 1rem;
  width: 70%;
  height: 10rem;
}

.skills-title {
  position: absolute;
  top: -1.3rem;
  background-color: rgb(34, 40, 49);
  border: 0.2rem solid rgb(0, 173, 181);
  border-radius: 0.4rem;
  padding: 0.3rem;
}

.skills-btn {
  background: rgb(34, 40, 49);
  color: beige;
  letter-spacing: 0.2rem;
  position: absolute;
  top: -1rem;
  left: -1rem;
  border: 0.2rem solid white;
  padding: 0.5rem;
  width: 6rem;
  transform: rotate(-20deg);
  cursor: pointer;
}

.skills-btn:hover {
  background: rgb(0, 173, 181);
  color: white;
}

.skills-content {
  width: 100%;
  margin: 2rem 0 0 3rem;
}

.skills-content li {
  margin-bottom: 1rem;
}

.about-notion {
  position: relative;
  width: 10rem;
  left: 45%;
  margin-top: 5rem;
  cursor: pointer;
}


.about-notion span::after {
  content: "자기소개 더보기 →";
  width: 100%;
  position: absolute;
  color: lightskyblue;
  top: 0;
  right: 0.15rem;
}

.about-notion::after {
  content: '';
  position: absolute;
  left: 0;
  bottom: -2px;
  width: 0;
  height: 2px;
  background-color: rgb(0, 173, 181);
  transition: 0.4s ease;
}

.about-notion:hover::after {
  width: 100%;
}

.about-container {
  display: flex;
  justify-content: center;
  font-weight: bold;
}

.about-content {
  display: grid;
  grid-template-columns: 1fr 1fr;
}

.about-text {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 2rem;
}

.about-text span {
  margin-top: 0.6rem;
  font-size: 1.2rem;
  font-family: var(--font);
}

/* project------------------------------------------------ */
.blue {
  border: 0.2rem solid rgb(155, 191, 235);
  margin-left: 50rem;
}

.blue .project-title {
  background-color: rgb(112, 133, 226);
  border: 0.2rem solid rgb(155, 191, 235);
}

.blue .project-icon {
  color: rgb(59, 136, 229);
}

.green {
  border: 0.2rem solid rgb(182, 232, 167);
  margin-left: 3rem;
}

.green .project-title {
  background-color: rgb(107, 207, 77);
  border: 0.2rem solid rgb(182, 232, 167);
}

.green .project-icon {
  color: rgb(141, 252, 107);
}

.project-period span {
  color: coral;
}

.project {
  background-color: black;
  font-weight: bold;
}

.project-header {
  color: rgb(99, 105, 105);
  font-size: 4rem;
  text-align: center;
}

.project-header::after {
  content: "Project";
  position: absolute;
  color: white;
  text-align: center;
  left: 40.6rem;
}

.project-container {
  height: 150vh;
}

.project-box {
  display: flex;
  width: 40rem;
  border-radius: 0.4rem;
  position: relative;
}

.project-content {
  width: 50rem;
}

.project-title {
  font-family: var(--font);
  font-weight: bold;
  position: absolute;
  top: -1.2rem;
  left: -1rem;
  padding: 0.3rem;
  border-radius: 0.2rem;
}

.project-period {
  color: #ccc;
  position: absolute;
  top: -1.2rem;
  right: 0.5rem;
  background-color: black;
  padding: 0.3rem;
}

.project-text {
  margin: 2rem 0 1rem 0.4rem;
  color: #ddd;
}

.project-text li {
  margin: 1rem 0 0 1rem;
}

.project-icon {
  display: flex;
  position: absolute;
  bottom: -1.5rem;
  right: 2rem;
  font-size: 2rem;
}

.link-box {
  margin-left: 5rem;
}

.icon-link {
  margin-left: 0.4rem;
}

.icon-git,
.icon-link {
  cursor: pointer;
}

.icon-git:hover,
.icon-link:hover {
  color: white;
}

.project-img {
  display: flex;
  padding: 1rem;
  width: 40rem;
  gap: 1rem;
}

.project-img img {
  width: 50%;
  border-radius: 0.2rem;
}

@media screen and (max-width: 440px) {
  nav {
    right: 8rem;
    position: fixed;
  }

  .navbar li {
    margin-right: 3rem;
  }

  .main-bg {
    display: block;
  }

  .main-left {
    width: 20rem;
    height: 30rem;
    margin: 0 auto;
  }

  .main-left img {
    margin: 3rem 0;
  }

  .main-right h1 {
    margin-top: 6rem;
    font-size: 0.7rem;
  }

  .main-text {
    margin-top: 2rem;
    font-size: 0.8rem;
  }

  .main-btn {
    margin-top: 3rem;
  }

  .about-header {
    font-size: 2.5rem;
  }

  .skills-header {
    display: flex;
    flex-wrap: wrap;
    margin-top: 4rem;
  }

  .skills-list {
    margin-top: 1rem;
  }

  .skills-container {
    display: block;
  }

  .skills-box {
    width: 90%;
    height: 100%;
  }

  .skills-btn {
    width: 4rem;
    margin: 0.2rem;
    left: -1.5rem;
  }

  .skills-content {
    width: 90%;
    margin: 2rem;
    height: 4rem;
  }

  .skills-content li {
    font-size: 0.7rem;
  }

  .about-notion {
    left: 30%;
  }

  .about-content {
    display: block;
  }

  .project {
    height: 220vh;
  }

  .project-header {
    color: rgb(99, 105, 105);
    font-size: 3rem;
    text-align: center;
  }

  .project-header::after {
    content: "Project";
    position: absolute;
    color: white;
    text-align: center;
    left: 7.6rem;
  }

  .project-box {
    display: block;
    width: 20rem;
  }

  .green,
  .blue {
    margin: 3rem auto;
  }

  .project-period {
    font-size: 0.5rem;
    right: -2rem;
    top: -1rem;
  }

  .project-text {
    font-family: var(--font);
    color: white;
  }

  .project-text li {
    margin: 0.7rem auto;
  }

  .project-icon {
    bottom: -1rem;
  }

  .link-box {
    display: flex;
  }

  .project-img {
    display: flex;
    width: 90%;
    gap: 0.5rem;
  }

}